<template>
  <div>
    <h3 v-if="shop">{{shop.name}}</h3>
    <h3 v-else>Shop Name</h3>

    <table class="table" v-if="shop">
      <tr>
        <th>Address</th>
        <td>:</td>
        <td>{{shop.address}}</td>
      </tr>
      <tr>
        <th>Phone</th>
        <td>:</td>
        <td>{{shop.phone}}</td>
      </tr>
      <tr>
        <th>Users</th>
        <td>:</td>
        <td>
          <el-tag
            style="margin:5px"
            v-for="user in shop.users" :key="user.user_id"
            type="info">{{user.name}} ({{user.phone}})</el-tag>
        </td>
      </tr>
    </table>

    <!-- <el-table></el-table> -->

    <el-row :gutter="8">
      <el-col :xs="{span: 24}" :sm="{span: 24}" :md="{span: 24}" :lg="{span: 8}" :xl="{span: 8}">
        <h4>Profit</h4>
        <app-line-chart
          :chart-data="profitStatistic"
          v-if="profitStatistic !== null"
        ></app-line-chart>
      </el-col>
      <el-col :xs="{span: 24}" :sm="{span: 24}" :md="{span: 24}" :lg="{span: 8}" :xl="{span: 8}">
        <h4>Transaction</h4>
        <app-line-chart
          :chart-data="transactionStatistic"
          v-if="transactionStatistic !== null"
        ></app-line-chart>
      </el-col>
      <el-col :xs="{span: 24}" :sm="{span: 24}" :md="{span: 24}" :lg="{span: 8}" :xl="{span: 8}">
        <h4>Stock</h4>
        <app-line-chart
          :chart-data="stockStatistic"
          v-if="stockStatistic !== null"
        ></app-line-chart>
        <div v-else>
          No Data
        </div>
      </el-col>
    </el-row>
  </div>
</template>


<script>
import LineChart from "@/components/Chart/LineChart";

export default {
  components: {
    appLineChart: LineChart,
  },
  data() {
    return {
      loading: false,

      profitStatistic: null,
      transactionStatistic: null,
      stockStatistic: null,
      shop: null,
    };
  },
  methods: {
    getShopStatistic() {
      this.$store
        .dispatch("FetchShopStatistic", this.$route.params.id)
        .then(res => {
          const data = res.data;
          const messages = data.messages;
          const status_code = data.status_code;
          this.profitStatistic = {
            chartData: data.data.statistic.profit_statistic,
            field: "profit",
          };
          this.transactionStatistic = {
            chartData: data.data.statistic.transaction_statistic,
            field: "transaction",
          };
          this.stockStatistic = {
            chartData: data.data.statistic.stock_statistic,
            field: "stock",
          };
        });
    },
    getShopDetail() {
      this.loading = true;
      this.$store
        .dispatch("FetchShop", this.$route.params.id)
        .then(res => {
          const data = res.data;
          const messages = data.messages;
          const status_code = data.status_code;
          this.shop = data.data.shop;
          this.loading = false;
        });
    }
  },
  created() {
    this.getShopStatistic();
    this.getShopDetail();
  }
};
</script>

<style lang="scss">
.table {
  width: 100%;

  tr {
    th, td {
      text-align: left;
      padding: 10px;
      border-bottom: 1px solid #eee;
    }
    td {

    }
  }
}
</style>




